:root {
  --contacts-border-color: #e1e1e1;
  --contacts-color-text-time: #ababab;
  --contacts-bg-card-color: #fff;
}

.contacts {
  background-color: var(--green-bg-color);
  padding-bottom: 40px;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.contacts__item {
  background-color: var(--white-content-color);
  border-radius: 24px;
  box-shadow: 0 8px 30px rgba(212, 20, 67, .1);
}

.contacts__text {
  background-color: var(--yellow-content-color);
  letter-spacing: .04em;
  color: var(--black-content-color);
  border-radius: 4px;
  margin-bottom: 20px;
  padding-left: 24px;
  padding-right: 24px;
  font-size: 12px;
  font-weight: 500;
  display: inline-block;
}

.address {
  margin-bottom: 48px;
}

.address__text {
  letter-spacing: .04em;
  color: var(--black-content-color);
  margin-bottom: 8px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.address__link {
  letter-spacing: .04em;
  color: var(--orange-products-bg-color);
  transition: border-color 400ms var(--timing-function);
  border-bottom: 1px solid rgba(0, 0, 0, 0);
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  text-decoration: none;
  position: relative;
}

.address__link:hover, .address__link:focus {
  border-color: var(--orange-products-bg-color);
}

.work-list {
  margin-bottom: 48px;
  position: relative;
}

.work-list__item:not(:last-child) {
  margin-bottom: 8px;
}

.work-list__day {
  color: var(--black-content-color);
  font-size: 12px;
  font-weight: 500;
}

.work-list__day:after {
  content: "";
  width: 100%;
  border: 1px solid var(--contacts-border-color);
  background-color: var(--contacts-border-color);
  position: absolute;
  top: -24px;
  left: 0;
}

.work-list__time {
  color: var(--contacts-color-text-time);
  font-size: 12px;
  font-weight: 500;
}

.work-list__time:after {
  content: "";
  width: 100%;
  border: 1px solid var(--contacts-border-color);
  background-color: var(--contacts-border-color);
  position: absolute;
  bottom: -24px;
  left: 0;
}

.contacts-page__info {
  color: var(--black-content-color);
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
}

.contacts-btn-list__button-locations {
  width: 196px;
  height: 42px;
  background-color: var(--yellow-content-color);
  cursor: pointer;
  color: var(--black-content-color);
  transition: background-color 400ms var(--timing-function), color 400ms var(--timing-function);
  border: none;
  border-radius: 10px;
  outline: none;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: 600;
  display: flex;
}

.contacts-btn-list__button-locations:hover, .contacts-btn-list__button-locations:focus {
  background-color: #0a8817;
}

.contacts-btn-list__button-locations:focus {
  color: var(--white-content-color);
}

.contacts-btn-list__button-franchise {
  width: 196px;
  height: 42px;
  border: 2px solid var(--yellow-content-color);
  cursor: pointer;
  color: var(--black-content-color);
  transition: border 400ms var(--timing-function), color 400ms var(--timing-function);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  outline: none;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: 600;
  display: flex;
}

.contacts-btn-list__button-franchise:hover, .contacts-btn-list__button-franchise:focus {
  border: 2px solid #0a8817;
}

.contacts-btn-list__button-franchise:focus {
  color: var(--white-content-color);
}

.contacts-btn-list__icon {
  height: 12px;
  width: 12px;
  stroke: var(--black-content-color);
  transition: stroke 400ms var(--timing-function);
  margin-left: 6px;
}

.contacts-btn-list__button-locations:focus .contacts-btn-list__icon, .contacts-btn-list__button-franchise:focus .contacts-btn-list__icon {
  stroke: var(--white-content-color);
}

